export default {
	data() {
		return {
			mapCtx: '',
			scale: 15,
			latitude: 0,
			longitude: 0,
			time: "00:00:00",
			h: 0,
			m: 0,
			s: 0,
			sec: 0,
			markers: [],
			polyline: [{
				points: [],
				color: "#ff0fff",
				width: 6,
			}],
			points: [],
			controls: [{
				id: 5,
				iconPath: '../../static/redPoint.png',
				position: {
					left: 200 - 10,
					top: 300 - 10,
					width: 10,
					height: 10
				},
				clickable: true
			}],
			km: 0,
			metters: 0,
			inter: null
		}
	},
	onLoad() {
		console.log("sportMap")
		my.showLoading();
		uni.getLocation({
			success:(res=>{
				console.log(res.latitude + res.longitude)
				my.hideLoading();
				this.latitude = res.latitude
				this.longitude = res.longitude
			}),
			fail:(res=>{
				my.hideLoading();
				my.alert({
					title: '定位失败'
				});
			})
		});
		this.markers = [
			// {latitude:25.102324,longitude:102.745521},
			// {latitude:25.106223,longitude:102.746984},
			// {latitude:25.102315,longitude:102.745568},
			// {latitude:25.101531,longitude:102.748045}
		]

		this.polyline[0].points = [
			// {latitude:25.102324,longitude:102.745521},
			// {latitude:25.106223,longitude:102.746984},
			// {latitude:25.102315,longitude:102.745568},
			// {latitude:25.101531,longitude:102.748045}
		]

	},
	computed: {
		speed() {
			if (this.metters) {
				const dateFamt = n => n > 10 ? n : '0' + n
				var time, route, speed, search, rep, fltNum, fltStr, m, s, speedStr
				time = (this.h * 60) + this.m + (this.s / 60)
				route = this.metters
				speed = time / route
				search = speed.toString()
				rep = search.replace('.', "'")
				fltNum = search.search("'")
				fltStr = search.slice(fltNum)
				m = parseInt(speed)
				s = Number(fltStr) * 6
				speedStr = `${m}'${dateFamt(s)}"`
			}
			return speedStr ? speedStr : `0'00"`
		}
	},
	methods: {
		// openLocation(){
		// 	 my.openLocation({
		// 		longitude: this.longitude,
		// 		latitude: this.latitude
		// 	})
		// },
		getLocation() {
			my.getLocation({
				success: (res) => {
					console.log("支付宝地图----更新的位置", res.latitude + res.longitude)
					this.latitude = res.latitude
					this.longitude = res.longitude
				}
			})
		},
		//更新缩放比例
		scaleChange() {
			this.scale = 15
		},
		GetDistance(lat1, lng1, lat2, lng2) {
			const Rad = d => d * Math.PI / 180 //经纬度转换成三角函数中度分表形式。
			var radLat1 = Rad(lat1);
			var radLat2 = Rad(lat2);
			var a = radLat1 - radLat2;
			var b = Rad(lng1) - Rad(lng2);
			var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) + Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(
				Math.sin(b / 2), 2)));
			s = s * 6378.137;
			s = (Math.round(s * 10000) / 10000) //输出为公里
			return s
		},
		setPoly() {
			if (this.inter != null) return
			this.inter = setInterval(() => {
				uni.getLocation({
					success: (res) => {
						let covers, points, len, len_index
						this.markers.push({
							latitude: res.latitude,
							longitude: res.longitude,
							iconPath: '../../static/redPoint.png'
						});
						this.polyline[0].points.push({
							latitude: res.latitude,
							longitude: res.longitude
						})
						points = this.polyline[0].points
						len = points.length
						len_index = points.length - 1
						//当数组的长度大于2 从右往左取
						if (len > 2) {
							let lat1, lat2, lng1, lng2
							lat1 = points[len_index - 1]['latitude']
							lng1 = points[len_index - 1]['longitude']
							lat2 = points[len_index]['latitude']
							lng2 = points[len_index]['longitude']
							this.km = (this.GetDistance(lat1, lng1, lat2, lng2)).toFixed(2)
							this.metters += Number(this.km);
						}
					}
				});
			}, 5000)
		},
		start() {
			const dateFamt = n => n > 10 ? n : '0' + n
			if (this.intervalId != null) return;
			this.intervalId = setInterval(() => {

				//实时更新定位
				this.getLocation()

				//计时器 累加时间 跑步时间
				this.sec++;
				let date = new Date(0, 0);
				date.setSeconds(this.sec);
				let h = date.getHours(),
					m = date.getMinutes(),
					s = date.getSeconds();
				this.h = h
				this.m = m
				this.s = s
				this.time = dateFamt(h) + ":" + dateFamt(m) + ":" + dateFamt(s);
			}, 1000);

			this.setPoly()
		},
		// 跑步结束
		stop() {
			clearInterval(this.intervalId)
			clearInterval(this.inter)
			this.intervalId = this.inter = null
		},
		//结束跑步,清空数据
		clearMsg() {
			this.time = "00:00:00"
			this.metters = 0,
				clearInterval(this.intervalId)
			clearInterval(this.inter)
			this.intervalId = this.inter = null
		}
	}
}
